.main{
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    min-height: 100vh;
    transition: 200ms;
    background-color: white;
    z-index: 2;
    transform: translateX(-100%);
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
}

.boxShadow{
    box-shadow: black 5px 5px 50px;
}


